import React from "react";
import {OrderActionsSection, OrderActionsSelect} from "./styled";

class GameListOrderActions extends React.Component<any, any> {
	render() {
		let {selectOrder, order} = this.props;

		return (
			<OrderActionsSection>
				<OrderActionsSelect defaultValue={order} onChange={selectOrder}>
					<option value="id">Select...</option>
					<option value="view">Lượt chơi</option>
					<option value="favorite">Yêu thích</option>
					<option value="releaseDate">Mới nhất</option>
				</OrderActionsSelect>
			</OrderActionsSection>
		);
	}
}

export default GameListOrderActions;
