import React from "react";
import CenterGameItem from "../../common/CenterGameItem";
import GameListOrderActions from "../../common/GameListOrderActions";
import GameListSearchAction from "../../common/GameListSearchAction";
import GameListQuickFilterActions from "../../common/GameListQuickFilterActions";
import {ApiConstants} from "../../../constant/Constants";
import axiosApiInstance from "../../../interceptor/CustomInterceptor";
import {MyPaginate, NotFoundGameSection} from "../../common/styled";
import {MobileGameList, MobileGameListActions} from "./styled";

class MobileCenterGameList extends React.Component<any, any> {
	constructor(props: any) {
		super(props);
		this.state = {
			error: null,
			isLoaded: false,
			data: [],
			keyword: "",
			order: "",
			currentPage: 0
		};
	}

	getGames = () => {
		const {keyword, currentPage, order} = this.state;
		const queryParams = {
			name: keyword,
			page: currentPage,
			sort: order + ',DESC',
			size: 15
		};
		axiosApiInstance.get(ApiConstants.GAME_LISTING_API, { params: queryParams })
			.then(result => {
					this.setState({
						isLoaded: true,
						data: result.data
					});
				})
			.catch(error => {
					this.setState({
						isLoaded: true,
						error
					});
				}
			);
	}

	componentDidMount() {
		this.getGames();
	}

	changeKeyword = (event: any) => {
		this.setState({
			keyword: event.target.value
		});
	}

	inputEnterPress = (event: any) => {
		if (event.key === 'Enter') {
			this.getGames();
		}
	}

	selectOrder = (event: any) => {
		this.setState({
				order: event.target.value
		}, () => {
			this.getGames();
		});
	}

	handlePageClick = (event: any) => {
		this.setState({
			currentPage: event.selected
		}, () => {
			this.getGames();
		});
	}

	render() {
		const { error, isLoaded, data, order } = this.state;
		if (error) {
			return <div>Error: {error.message}</div>;
		} else if (!isLoaded) {
			return <div>Loading...</div>;
		} else if(data.data.length === 0) {
			return (
				<>
					<GameListSearchAction inputEnterPress={this.inputEnterPress} inputChangeHandle={this.changeKeyword}
					                      searchFunction={this.getGames}/>
					<GameListQuickFilterActions/>
					<GameListOrderActions selectOrder={this.selectOrder} order={order} />
					<NotFoundGameSection>
						<span>Game không tồn tại!</span>
					</NotFoundGameSection>
				</>
			);
		}
		return (
			<>
				<MobileGameListActions>
					<GameListSearchAction inputEnterPress={this.inputEnterPress} inputChangeHandle={this.changeKeyword}
					                      searchFunction={this.getGames}/>
					<GameListQuickFilterActions/>
					<GameListOrderActions selectOrder={this.selectOrder} order={order} />
				</MobileGameListActions>
				<MobileGameList>
					{data.data.map((game: any) => (
						<CenterGameItem key={game.id} gameData={game} />
					))}
				</MobileGameList>
				<MyPaginate
					breakLabel="..."
					nextLabel="Next"
					onPageChange={this.handlePageClick}
					pageRangeDisplayed={data.size}
					pageCount={data.totalPages}
					previousLabel="Previous"
				/>
			</>
		);
	}
}

export default MobileCenterGameList;
