import React from "react";
import {SearchInput, SearchSection} from "./styled";
import {ReactComponent as SearchIcon} from "../../icons/search.svg";

class GameListSearchAction extends React.Component<any, any> {
	render() {
		let {searchFunction, inputChangeHandle, inputEnterPress} = this.props;
		return (
			<SearchSection>
				<SearchInput onChange={inputChangeHandle} onKeyPress={inputEnterPress} placeholder={"Search Game..."} />
				<SearchIcon onClick={searchFunction} width={18} height={18} fill={"#6a6a70"} style={{marginLeft: "-25px", cursor: "pointer"}} />
			</SearchSection>
		);
	}
}

export default GameListSearchAction;
