import React from 'react';
import './App.css';
import ComputerGameCenter from './components/computer/center/ComputerGameCenter';
import MobileGameCenter from "./components/mobile/center/MobileGameCenter";
import GlobalFonts from "./fonts/GlobalFonts";

const useViewport = () => {
  const [width, setWidth] = React.useState(window.innerWidth);

  React.useEffect(() => {
    const handleWindowResize = () => setWidth(window.innerWidth);
    window.addEventListener("resize", handleWindowResize);
    return () => window.removeEventListener("resize", handleWindowResize);
  }, []);

  return { width };
};

function App() {
  const viewPort = useViewport();
  const isMobile = viewPort.width <= 1024;
  if (isMobile) {
    return (
      <MobileGameCenter name={"Tai on Mobile"} />
    );
  }
  return (
    <ComputerGameCenter name={"Tai on Computer"} />
  );
}

export default App;
