import React, {useState} from "react";
import {
	ActionLabel,
	FavoriteAction,
	GameActionItems,
	GameActions,
	GameItem, GameItemOverlay,
	GameName, PlayGameButton,
	ViewAction
} from "./styled";
import {ReactComponent as HeartIcon} from "../../icons/heart.svg";
import {ReactComponent as EyeIcon} from "../../icons/eye.svg";
import {ReactComponent as PlayIcon} from "../../icons/play.svg";
import axiosApiInstance from "../../interceptor/CustomInterceptor";
import {ApiConstants} from "../../constant/Constants";

class CenterGameItem extends React.Component<any, any> {
	constructor(props: any) {
		super(props);
	}

	addToFavorite = (gameData: any) => {
		axiosApiInstance.put(ApiConstants.GAME_ADD_FAVORITE_API, null, { params: {gameId: gameData.id} })
			.then(result => {
				const favoriteActionElements = document.getElementById(`favorite-action-${gameData.id}`);

				const iconElement = favoriteActionElements?.firstElementChild;
				const labelElement = favoriteActionElements?.lastElementChild;

				const labelTitle = labelElement?.getAttribute('title');
				// Split favorite amount
				const titleArrs = labelTitle?.split(' ');
				const currentValue = Number.parseInt(titleArrs ? titleArrs[0] : '0');

				if(iconElement && iconElement.firstElementChild) {
					iconElement.firstElementChild.textContent = `${currentValue + 1} lượt thích`;
				}
				labelElement?.setAttribute('title', `${currentValue + 1} lượt thích`);

				if(currentValue < 1000 && labelElement) {
					labelElement.textContent = `${currentValue + 1}`;
				}
			});
	}

	render() {
		const {gameData} = this.props;

		function nFormatter(num: number, digits: number) {
			if(num < 1000) {
				return num;
			}

			const lookup = [
				{ value: 1, symbol: "" },
				{ value: 1e3, symbol: "k" },
				{ value: 1e6, symbol: "M" },
				{ value: 1e9, symbol: "G" },
				{ value: 1e12, symbol: "T" },
				{ value: 1e15, symbol: "P" },
				{ value: 1e18, symbol: "E" }
			];
			const rx = /\.0+$|(\.[0-9]*[1-9])0+$/;
			var item = lookup.slice().reverse().find(function(item) {
				return num >= item.value;
			});
			return item ? (num / item.value).toFixed(digits).replace(rx, "$1") + item.symbol : "0";
		}

		return (
			<GameItem>
				<a href={gameData.gameUrl} target="_blank" rel="noreferrer"
					style={{textDecoration: "none"}}>
					<GameItemOverlay>
						<PlayGameButton>
							<PlayIcon width={52} height={52} fill={"#ffffff"} />
						</PlayGameButton>
					</GameItemOverlay>
					<img src={gameData.thumbnail} alt={gameData.name} style={{
						height: "270px",
						width: "320px",
						borderRadius: "8px",
					}}/>
				</a>
				<GameActionItems>
					<GameName>
						<span>{gameData.name}</span>
					</GameName>
					<GameActions>
						<FavoriteAction id={`favorite-action-${gameData.id}`}>
							<HeartIcon onClick={() => this.addToFavorite(gameData)}
							           className={"icon-hover"}
							           title={`${gameData.favorite} lượt thích`}
							           width={14} height={14} fill={"#9e9ea7"}/>
							<ActionLabel title={`${gameData.favorite} lượt thích`}>{nFormatter(gameData.favorite, 1)}</ActionLabel>
						</FavoriteAction>
						<ViewAction>
							<EyeIcon
								className={"icon-hover"}
								title={`${gameData.view} lượt chơi`}
								width={16} height={16} fill={"#9e9ea7"}/>
							<ActionLabel title={`${gameData.view} lượt chơi`}>{nFormatter(gameData.view, 1)}</ActionLabel>
						</ViewAction>
					</GameActions>
				</GameActionItems>
			</GameItem>
		);
	}
}

export default CenterGameItem;
