import {ApiConstants, HostConstant} from "../constant/Constants";
import Cookies from "universal-cookie";
import axios from "axios";

const axiosApiInstance = axios.create({
	baseURL: HostConstant.HOST_DOMAIN
});

axiosApiInstance.interceptors.request.use(
	async request => {
		if(request.url?.includes(ApiConstants.ANONYMOUS_LOGIN_API)) {
			return request;
		}
		const cookie = new Cookies();
		let accessToken = cookie.get("accessToken");
		if(!accessToken) {
			const response = await axiosApiInstance.post(ApiConstants.ANONYMOUS_LOGIN_API);
			accessToken = response.data.accessToken;
			const expiresIn = new Date(response.data.expiresIn);
			const cookie = new Cookies();
			cookie.set("accessToken", accessToken, {path: "/", expires: expiresIn});
		}
		request.headers = {
			'Authorization': `Bearer ${accessToken}`
		}
		return request;
	},
	error => {
		return Promise.reject(error);
	}
);

axiosApiInstance.interceptors.response.use(
	(response) => {
		return response;
	},
	(error) => {
		const originalRequest = error.config;

		if (error.response.status === 401 && !originalRequest._retry) {
			originalRequest._retry = true;

			return axiosApiInstance.post(ApiConstants.ANONYMOUS_LOGIN_API).then((res) => {
				if (res.status === 200) {
					const cookie = new Cookies();
					cookie.set("accessToken", res.data.accessToken, {path: "/", expires: res.data.expiresIn});
					return axiosApiInstance(originalRequest);
				}
			});

		} else {
			return Promise.reject(error);
		}
	}
);

export default axiosApiInstance;
